import React, { PureComponent } from 'react'
import { Table, message } from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { countTableScroll } from 'util'
import style from './style/Table.scss'
@connect(
  state => ({ ...state.diffApportionment, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
    this.columns = [
      {
        title: '公司',
        width: 150,
        dataIndex: 'company',
        key: 'company',
        render: (text, record) => (
            <div>
              {this.renderCodeBtn(text, record)}
            </div>
          )
      },
      {
        title: '期间',
        width: 100,
        dataIndex: 'period',
        key: 'period'
      },
      {
        title: '状态',
        width: 80,
        dataIndex: 'statusDesc',
        key: 'statusDesc'
      },
      {
        title: '完成时间',
        width: 150,
        dataIndex: 'finishTime',
        key: 'finishTime'
      },
      {
        title: '创建时间',
        width: 150,
        dataIndex: 'createTime',
        key: 'createTime'
      },
      {
        title: '创建人员',
        width: 100,
        dataIndex: 'createBy',
        key: 'createBy'
      }
    ]
  }
  renderCodeBtn(text, record, index) {
    const { companyName = '' } = record
    let dom = <div
          className={style.link_in_table}
          onClick={this.linkToDet.bind(this, record)}
        >
          {text} {companyName}
        </div>
    return dom
  }
  linkToDet = (record) => {
    const { history, menuPermissionIds } = this.props
    const { company = '', id = '', period = '', companyName = '', status = '' } = record
        // diffApportionment_diff_apportionment: 差异分摊  diffApportionment_diff_post: 差异过账
    const flag_apportionmentInTable = menuPermissionIds && menuPermissionIds.includes('diffApportionment_diff_apportionment') ? 'allowApportionment' : 'forbiddenApportionment'
    const flag_postTable = menuPermissionIds && menuPermissionIds.includes('diffApportionment_diff_post') ? 'allowPost' : 'forbiddenPost'
    const flag_checkData = menuPermissionIds && menuPermissionIds.includes('diffApportionment_checkData') ? 'allowCheckData' : 'forbiddenCheckData'
    return history.push(
      `/mms/costCenter/diffApportionment/det/${id}?editOrCreate=edit&period=${period}&statusCode=${status}&company=${company}&flag_apportionmentInTable=${flag_apportionmentInTable}&flag_postTable=${flag_postTable}&flag_checkData=${flag_checkData}&companyName=${encodeURIComponent(companyName) || ''}`
    )
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size } = this.props
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
 // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    const newUserLoationValue = newProps && newProps.userLoation && newProps.userLoation.value || ''
    const userLoationValue = userLoation && userLoation.value || ''
    if (newUserLoationValue != userLoationValue) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter) => {
    const { handleGetList, searchParams, page, size } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      let reqObj = { ...searchParams }
      for (let i in reqObj) {
        if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
          console.log('reqObj[i] is', reqObj[i])
          reqObj[i] = reqObj[i].value
        }
      }
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...reqObj
        }
      })
    }
  }
  // 需求数据明细
  renderResourceData = () => (
    <div className={style.tr_in_table}>
      <tr>
        <th className={style.td_in_table_one}>生产需求单号</th>
        <th className={style.td_in_table_one}>需求量</th>
      </tr>
      {[1, 2, 3, 4].map(() => (
        <tr>
          <td>0</td>
          <td>1</td>
        </tr>
      ))}
    </div>
  )
  // 导出
  handleExport = () => {
    const { handleExport, searchParams, page, size } = this.props
    handleExport({ searchParams: { page, size, ...searchParams } }, () => {
      message.success('导出成功')
    })
  }
  render() {
    const { total, page, size, tableLoading, searchResult = [] } = this.props
    let pagination = {
      total,
      showTotal: total => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['10', '50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={record => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: countTableScroll(this.columns) }}
          onChange={this.onChange}
          pagination={pagination}
          // rowSelection={rowSelection}
        />
      </div>
    )
  }
}



