import { Bread } from 'Bread'
import React, { PureComponent } from 'react'
import Form from './components/./Form'
import Table from './components/./Table'
import Tool from './components/./Tool'
import { connect } from 'react-redux'
import { mapDispatchToProps } from './redux/mapDispatchToProps'

@connect(
  state => ({
    ...state.diffApportionment,
    menuPermissionIds: state.menuPermissionIds
  }),
  mapDispatchToProps
)
export default class App extends PureComponent {
  constructor(props) {
    super(props)
  }
  componentWillUnmount() {
    this.props.update({ searchResult: [], searchParams: {} })
  }
  render() {
    const { history, menuPermissionIds } = this.props
    return (
      <section>
        <Bread route={['成本中心', '月底差异分摊']} />
        <Form menuPermissionIds={menuPermissionIds} />
        <Tool menuPermissionIds={menuPermissionIds} history={history} />
        <Table menuPermissionIds={menuPermissionIds} history={history} />
      </section>
    )
  }
}



