import React, { PureComponent } from 'react'
import { Form, Button, Row, Col, DatePicker, Checkbox } from 'antd'
import style from './style/Form.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import { CsxSelect } from 'Form'
import { API_DIFFAPPORTIONMENTS_COMPANY_LIST } from 'api'
const MonthPicker = DatePicker.MonthPicker
import moment from 'moment'
@connect(
  state => ({ ...state.diffApportionment }),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      checked: false
    }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const { searchParams, update } = this.props
    update({ searchParams: { ...searchParams, ...values } })
  }
  // 查询数据
  query = () => {
    const { handleGetList, searchParams, size } = this.props
    let reqObj = { ...searchParams }
    for (let i in reqObj) {
      if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
        console.log('reqObj[i] is', reqObj[i])
        reqObj[i] = reqObj[i].value
      }
    }
    delete reqObj.initTime
    handleGetList({ searchParams: { ...reqObj, page: 1, size } })
  }
  onChange111 = e => {
    console.log('checked = ', e.target.checked)
    this.onInputChange({ checked: e.target.checked })
  };
  // 重置
  reset = () => {
    const { update } = this.props
    update({ searchParams: {} })
  }
  render() {
    const { searchParams = {} } = this.props
    const { initTime, companyCode } = searchParams
    return (
      <Form className={style.form}>
        <Row type='flex' justify='start'>
          <Col span={8}>
            <CsxSelect
              dateApi={API_DIFFAPPORTIONMENTS_COMPANY_LIST}
              label='公司'
              allowClear={true}
              selectItem={{
                data: companyCode,
                key: 'code',
                valueKey: ['code', 'name']
              }}
              onChangeSelect={value => {
                this.onInputChange({ companyCode: value })
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>期间：</label>
              <div className='box' style={{ marginRight: 0 }}>
                <MonthPicker
                  onChange={time => {
                    let period = time && `${moment(time).format('YYYY-MM')}`
                    this.onInputChange({
                      initTime: time,
                      period: period || ''
                    })
                  }}
                  format={'YYYY-MM'}
                  value={initTime}
                />
              </div>
            </div>
          </Col>
          <Col span={8}>
            <Row type='flex' justify='end'>
            <Button
              type='primary'
              className={style.mr10}
              onClick={this.query}
            >
              查询
            </Button>
            <Button type='primary' onClick={this.reset}>
              重置
            </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



