import React, { PureComponent } from 'react'
import { Button } from 'antd'
import {} from 'api'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
@connect(
  state => ({ ...state.diffApportionment }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor(props) {
    super(props)
    this.state = {}
  }
  // 新增
  handleAdd = () => {
    const { history, menuPermissionIds } = this.props
    // diffApportionment_diff_apportionment: 差异分摊  diffApportionment_diff_post: 差异过账
    const flag_apportionmentInTable = menuPermissionIds && menuPermissionIds.includes('diffApportionment_diff_apportionment') ? 'allowApportionment' : 'forbiddenApportionment'
    const flag_postTable = menuPermissionIds && menuPermissionIds.includes('diffApportionment_diff_post') ? 'allowPost' : 'forbiddenPost'
    const flag_checkData = menuPermissionIds && menuPermissionIds.includes('diffApportionment_checkData') ? 'allowCheckData' : 'forbiddenCheckData'
    history.push(
      `/mms/costCenter/diffApportionment/create?editOrCreate=create&flag_apportionmentInTable=${flag_apportionmentInTable}&flag_postTable=${flag_postTable}&flag_checkData=${flag_checkData}`
    )
  }
  render() {
    const { menuPermissionIds = [] } = this.props
    return (
      <div className={style['table-tool']}>
        {menuPermissionIds && menuPermissionIds.includes('diffApportionment_add') && (
          <Button type='primary' className={style.btn} onClick={this.handleAdd}>
          新增
        </Button>
        )}
      </div>
    )
  }
}



